a {
    text-decoration: none;
  }
  
  *,
  *::before,
  *::after {
    box-sizing: border-box;
  }
  
  body {
    font-family: "Ubuntu", "Arial", sans-serif;
    font-size: 14px;
    font-weight: 400;
    line-height: 21px;
    color: #000000;
    word-wrap: break-word;
    margin: 0;
  }
  
  img {
    max-width: 100%;
  }